:root {
  --bg-primary: #087167;
  --bg-secondary: #065a4f;
  --text-primary: #FAF5E0;
  --text-secondary: rgba(250, 245, 224, 0.8);
  --accent: #FAF5E0;
  --spacing-xs: clamp(1rem, 2vw, 1.25rem);
  --spacing-sm: clamp(1.5rem, 3vw, 2rem);
  --spacing-md: clamp(2.5rem, 5vw, 3.5rem);
  --spacing-lg: clamp(4rem, 7vw, 6rem);
  --spacing-xl: clamp(6rem, 10vw, 10rem);
  --container: min(1400px, 90vw);
  --radius-sm: 4px;
  --radius-md: 6px;
  --radius-lg: 8px;
  --transition: 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

html {
  scroll-behavior: smooth;
  background: var(--bg-primary);
  height: 100%;
}

body {
  font-family: 'Instrument Serif', serif;
  color: var(--text-primary);
  line-height: 1.6;
  min-height: 100%;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
}

.container {
  width: var(--container);
  margin: 0 auto;
  padding: 0 var(--spacing-sm);
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 400;
  line-height: 1.2;
  text-align: center;
}

h1 {
  font-size: clamp(2.5rem, 5vw, 4.5rem);
  letter-spacing: -0.02em;
}

h2 {
  font-size: clamp(2rem, 4vw, 3.5rem);
  letter-spacing: -0.02em;
}

p {
  color: var(--text-secondary);
  font-size: clamp(1rem, 2vw, 1.125rem);
  text-align: center;
}

a {
  color: inherit;
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}
