.hero {
  min-height: 90vh;
  display: flex;
  align-items: center;
  padding: var(--spacing-xl) 0;
  position: relative;
  overflow: hidden;
}

.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  background: radial-gradient(circle at 30% 30%, rgba(250, 245, 224, 0.08), transparent 60%);
  pointer-events: none;
}

.hero-grid {
  display: grid;
  grid-template-columns: 1fr;
  position: relative;
  align-items: center;
  padding-left: 0;
}

.hero-content {
  animation: fadeIn 1s cubic-bezier(0.4, 0, 0.2, 1);
  max-width: 750px;
  margin-left: 0;
  position: relative;
  z-index: 2;
  padding-left: var(--spacing-md);
  text-align: left;
  margin-right: auto;
}

.hero h1 {
  margin-bottom: var(--spacing-md);
  font-size: clamp(3rem, 6vw, 5rem);
  line-height: 1.1;
  letter-spacing: -0.02em;
  position: relative;
}

.hero p {
  max-width: 36rem;
  margin: 0 0 var(--spacing-lg);
  font-size: clamp(1.25rem, 2vw, 1.5rem);
  line-height: 1.5;
  opacity: 0.9;
}

.hero-cta-group {
  display: flex;
  gap: var(--spacing-sm);
  justify-content: flex-start;
}

.cta-primary {
  background: var(--text-primary);
  color: var(--bg-primary);
  padding: 1rem 2rem;
  border-radius: var(--radius-sm);
  display: inline-flex;
  align-items: center;
  gap: 0.75rem;
  font-weight: 500;
  transition: all var(--transition);
}

.cta-primary:hover {
  background: rgba(250, 245, 224, 0.9);
}

.cta-icon {
  width: 1.25rem;
  height: 1.25rem;
  transition: transform var(--transition);
}

.cta-primary:hover .cta-icon {
  transform: translateX(4px);
}

.cta-secondary {
  padding: 1rem 2rem;
  border: 1px solid rgba(250, 245, 224, 0.2);
  border-radius: var(--radius-sm);
  transition: all var(--transition);
}

.cta-secondary:hover {
  border-color: var(--text-primary);
  background: rgba(250, 245, 224, 0.05);
}

.hero-visual {
  position: absolute;
  top: 50%;
  left: 70%;
  transform: translate(-50%, -50%);
  width: 140%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
}

.geometric-overlay {
  width: 150%;
  max-width: 800px;
  opacity: 0;
  animation: fadeIn 1s cubic-bezier(0.4, 0, 0.2, 1) 0.4s forwards;
}

@keyframes ripple1 {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-8px); }
}

@keyframes ripple2 {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(8px); }
}

@keyframes ripple3 {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-5px); }
}

.geometric-overlay path:nth-of-type(4) {
  animation: ripple1 8s ease-in-out infinite;
}

.geometric-overlay path:nth-of-type(5) {
  animation: ripple2 7s ease-in-out infinite;
}

.geometric-overlay path:nth-of-type(6) {
  animation: ripple3 9s ease-in-out infinite;
}

.geometric-overlay path:nth-of-type(7) {
  animation: ripple2 6s ease-in-out infinite;
}

@media (max-width: 1024px) {
  .hero-grid {
    padding: 0 var(--spacing-md);
  }

  .hero-content {
    margin: 0 auto;
    padding-left: 0;
    text-align: center;
  }
  
  .geometric-overlay {
    width: 200%;
    max-width: none;
  }
  
  .hero-visual {
    left: 50%;
    width: 180%;
  }

  .hero-cta-group {
    justify-content: center;
  }
}