.programs {
  padding: var(--spacing-xl) 0;
  position: relative;
  margin: var(--spacing-xl) 0;
}

.programs::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  background: linear-gradient(90deg, rgba(250, 245, 224, 0.03) 0%, transparent 100%);
  pointer-events: none;
}

.section-header {
  text-align: center;
  max-width: 700px;
  margin: 0 auto var(--spacing-xl);
}

.section-header h2 {
  margin-bottom: var(--spacing-md);
  font-size: 2.5rem;
  letter-spacing: -0.03em;
  line-height: 1.2;
}

.programs-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: var(--spacing-md);
}

.program-card {
  background: transparent;
  border: 1px solid rgba(250, 245, 224, 0.08);
  border-radius: var(--radius-sm);
  padding: var(--spacing-lg);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: var(--spacing-lg);
  transition: all var(--transition);
}

.program-card:hover {
  border-color: rgba(250, 245, 224, 0.15);
  background: rgba(250, 245, 224, 0.02);
}

.program-content {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-md);
  text-align: left;
}

.icon-wrapper {
  width: 40px;
  height: 40px;
  background: transparent;
  border: 1px solid rgba(250, 245, 224, 0.08);
  border-radius: var(--radius-sm);
  display: flex;
  align-items: center;
  justify-content: center;
}

.program-icon {
  width: 20px;
  height: 20px;
  color: var(--text-primary);
  opacity: 0.9;
}

.program-card h3 {
  font-size: 1.5rem;
  margin: 0;
  letter-spacing: -0.02em;
  line-height: 1.3;
}

.program-card p {
  font-size: 1.125rem;
  margin: var(--spacing-sm) 0 0;
  line-height: 1.6;
  opacity: 0.9;
}

.program-link {
  display: inline-flex;
  align-items: center;
  gap: 0.75rem;
  color: var(--text-primary);
  font-weight: 400;
  font-size: 1.125rem;
  transition: all var(--transition);
  margin-top: var(--spacing-md);
}

.arrow-icon {
  width: 18px;
  height: 18px;
  opacity: 0.8;
  transition: all var(--transition);
}

.program-link:hover {
  gap: 1rem;
}

.program-link:hover .arrow-icon {
  transform: translateX(4px);
  opacity: 1;
}

@media (max-width: 768px) {
  .programs-grid {
    grid-template-columns: 1fr;
  }
  
  .program-card:hover {
    transform: translateY(-4px);
  }
}
