.navigation {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  padding: 1rem 0;
  background: rgba(8, 113, 103, 0.97);
  backdrop-filter: blur(8px);
  border-bottom: 1px solid rgba(250, 245, 224, 0.08);
}

.nav-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 2rem;
  max-width: 1400px;
  margin: 0 auto;
}

.logo {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.logo-text {
  font-size: 2rem;
  letter-spacing: -0.02em;
  font-weight: 600;
}

.nav-content {
  display: flex;
  align-items: center;
  gap: 3rem;
}

.nav-links {
  display: flex;
  gap: 2.5rem;
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav-links a {
  opacity: 0.9;
  transition: all var(--transition);
  font-size: 1.75rem;
  letter-spacing: -0.01em;
  font-weight: 500;
}

.nav-links a:hover {
  opacity: 1;
}

.nav-cta {
  background: var(--text-primary);
  color: var(--bg-primary);
  padding: 0.75rem 2rem;
  border-radius: var(--radius-sm);
  font-weight: 500;
  font-size: 1.25rem;
  letter-spacing: -0.01em;
  transition: all var(--transition);
}

.nav-cta:hover {
  background: rgba(250, 245, 224, 0.95);
  transform: translateY(-1px);
}

@media (max-width: 768px) {
  .nav-links {
    display: none;
  }
  
  .nav-cta {
    display: none;
  }
}
